import { JSBI, Token } from '@pancakeswap-libs/sdk'
import React, { useCallback, useContext, useMemo, useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import styled, { ThemeContext } from 'styled-components'
import TokenAddressInputPanel from 'components/TokenAddressInputPanel'
import TransactionIdInputPanel from 'components/TransactionIdInputPanel'
import TranslatedText from 'components/TranslatedText'
import { AutoColumn } from '../../components/Column'
import { AutoRow } from '../../components/Row'
import { SearchInput } from '../../components/SearchModal/styleds'
import { useAllTokenV1Exchanges } from '../../data/V1'
import { useActiveWeb3React } from '../../hooks'
import { useAllTokens, useToken } from '../../hooks/Tokens'
import { useSelectedTokenList } from '../../state/lists/hooks'
import { useTokenBalancesWithLoadingIndicator } from '../../state/wallet/hooks'
import { BackArrow, Button, TYPE } from '../../components/Shared'
import { LightCard } from '../../components/Card'
import { BodyWrapper } from '../AppBody'
import { EmptyState } from './EmptyState'
import V1PositionCard from '../../components/PositionCard/V1'
import QuestionHelper from '../../components/QuestionHelper'
import { Dots } from '../../components/swap/styleds'
import { useAddUserToken } from '../../state/user/hooks'
import { isTokenOnList } from '../../utils'



const { mediumHeader: MediumHeader, body: Body } = TYPE

const TransactionDetailBody = styled.div`
  text-align: center;
  margin: auto;
  padding: 20px 10px;

  & > div {
    margin-bottom: 10px;
  }


  #join-pool-button{
    display: inline-block;
  }

`
export default function TransactionDetail() {
  const theme = useContext(ThemeContext)
  const { account, chainId } = useActiveWeb3React()

  const [tokenSearch, setTokenSearch] = useState<string>('')
  const handleTokenSearchChange = useCallback((e) => setTokenSearch(e.target.value), [setTokenSearch])

  // automatically add the search token
  const token = useToken(tokenSearch)
  const selectedTokenListTokens = useSelectedTokenList()
  const isOnSelectedList = isTokenOnList(selectedTokenListTokens, token ?? undefined)
  const allTokens = useAllTokens()
  const addToken = useAddUserToken()
  useEffect(() => {
    if (token && !isOnSelectedList && !allTokens[token.address]) {
      addToken(token)
    }
  }, [token, isOnSelectedList, addToken, allTokens])

  // get V1 LP balances
  const V1Exchanges = useAllTokenV1Exchanges()
  const V1LiquidityTokens: Token[] = useMemo(() => {
    return chainId
      ? Object.keys(V1Exchanges).map(
          (exchangeAddress) => new Token(chainId, exchangeAddress, 18, 'UNI-V1', 'Uniswap V1')
        )
      : []
  }, [chainId, V1Exchanges])
  const [V1LiquidityBalances, V1LiquidityBalancesLoading] = useTokenBalancesWithLoadingIndicator(
    account ?? undefined,
    V1LiquidityTokens
  )
  const allV1PairsWithLiquidity = V1LiquidityTokens.filter((V1LiquidityToken) => {
    const balance = V1LiquidityBalances?.[V1LiquidityToken.address]
    return balance && JSBI.greaterThan(balance.raw, JSBI.BigInt(0))
  }).map((V1LiquidityToken) => {
    const balance = V1LiquidityBalances[V1LiquidityToken.address]
    return balance ? (
      <V1PositionCard
        key={V1LiquidityToken.address}
        token={V1Exchanges[V1LiquidityToken.address]}
        V1LiquidityBalance={balance}
      />
    ) : null
  })

  // should never always be false, because a V1 exhchange exists for WETH on all testnets
  const isLoading = Object.keys(V1Exchanges)?.length === 0 || V1LiquidityBalancesLoading
  const [tokenAddress, setTokenAddress] = useState('');
  const [transactionId, setTransactionId] = useState('');

  // Logic taken from Web3Status/index.tsx line 175

  const onChangeTokenAddress = (res) => {
    setTokenAddress(res)
  }
  const onChangeTransactionId = (res) => {
    setTransactionId(res)
  }
  return (
    <BodyWrapper>

      <AutoRow style={{ alignItems: 'center', justifyContent: 'space-between', margin: '0px', borderBottom: "1px solid #524B63" }} gap="8px">
        <BackArrow to="/pool" />
        <MediumHeader>Confirm transaction</MediumHeader>
        <div>
          <QuestionHelper text="To create a Recovery Pool which will recover value for your worthless tokens, we need the token address and the transaction id when you bought the token.
      The transaction id will be used to determine the value of the token as at when you bought it" />
        </div>
      </AutoRow>
      <AutoColumn gap="lg" justify="center">
        <TransactionDetailBody>
          <TransactionIdInputPanel id='transaction-id-input' value={transactionId} onChange={onChangeTransactionId} />
          <TokenAddressInputPanel id='token-address-input' value={tokenAddress} onChange={onChangeTokenAddress} />
          <Button id="join-pool-button" as={Link} to="/tx-detail">
            <TranslatedText translationId={100}>Create Recovery Pool</TranslatedText>
          </Button>
        </TransactionDetailBody>
        
      </AutoColumn>
    </BodyWrapper>
  )
}
