import React, { useCallback, useContext, useState } from 'react'
// import { Link } from 'react-router-dom'
import { CheckmarkCircleIcon, ErrorIcon, Flex, LinkExternal, Text, Modal, Button, Input } from '@pancakeswap-libs/uikit'
import { useActiveWeb3React } from 'hooks'
import { TransactionDetails } from 'state/transactions/reducer'
import Loader from 'components/Loader'
import styled, { ThemeContext } from 'styled-components'
import TokenAddressInputPanel from 'components/TokenAddressInputPanel'
import TransactionIdInputPanel from 'components/TransactionIdInputPanel'
import TranslatedText from 'components/TranslatedText'

const Margined = styled.div`
  margin-top: 10px;
`

const Modalfooter = styled.div`
  text-align: center;

  #create-pool-button {
    margin-right:10px
  }
`

const Discription = styled.p`
  color: ${({ theme }) => theme.colors.text};

  ${({ theme }) => theme.mediaQueries.sm} {
    width: 80vw;
  }
  ${({ theme }) => theme.mediaQueries.nav} {
    width: 60vw;
  }
  ${({ theme }) => theme.mediaQueries.md} {
    width: 50vw;
  }
  ${({ theme }) => theme.mediaQueries.lg} {
    width: 40vw;
  }
`

type RecentTransactionsModalProps = {
  onDismiss?: () => void
}

// TODO: Fix UI Kit typings
const defaultOnDismiss = () => null

const newTransactionsFirst = (a: TransactionDetails, b: TransactionDetails) => b.addedTime - a.addedTime

const getRowStatus = (sortedRecentTransaction: TransactionDetails) => {
  const { hash, receipt } = sortedRecentTransaction

  if (!hash) {
    return { icon: <Loader />, color: 'text' }
  }

  if (hash && receipt?.status === 1) {
    return { icon: <CheckmarkCircleIcon color="success" />, color: 'success' }
  }

  return { icon: <ErrorIcon color="failure" />, color: 'failure' }
}

const TransactionDetailsModal = ({ onDismiss = defaultOnDismiss }: RecentTransactionsModalProps) => {
  const { account, chainId } = useActiveWeb3React()
  const theme = useContext(ThemeContext)
  console.log("sssssssssssss", theme)
  const [tokenAddress, setTokenAddress] = useState('');
  const [transactionId, setTransactionId] = useState('');

  // Logic taken from Web3Status/index.tsx line 175

  const onChangeTokenAddress = (res) => {
    setTokenAddress(res)
  }
  const onChangeTransactionId = (res) => {
    setTransactionId(res)
  }


  // const error = Boolean(value.length > 0 && !loading && !address)

  return (
    <Modal title="Create Recovery Pool" onDismiss={onDismiss}>
      <Discription>
        To create a Recovery Pool which will recover value for your worthless tokens, we need the token address and the transaction id when you bought the token.
        The transaction id will be used to determine the value of the token as at when you bought it
      </Discription>
      <Margined />
      <TokenAddressInputPanel id='token-address-input' value={tokenAddress} onChange={onChangeTokenAddress} />
      <Margined />
      <TransactionIdInputPanel id='transaction-id-input' value={transactionId} onChange={onChangeTransactionId} />
      <Margined />
      <Modalfooter>
        <Button id="create-pool-button" to="/add/ETH">
          <TranslatedText translationId={100}>Create</TranslatedText>
        </Button>
      </Modalfooter>
      {/* <>To create a Recovery Pool which will recover value for your worthless tokens, we need the token address and the transaction id when you bought the token. The transaction id will be used to determine the value of the token as at when you bought it</> */}
    </Modal>
  )
}

export default TransactionDetailsModal
